function passer(use) {
    if (use === 'vtr') {
        return 'https://i.pinimg.com/736x/f3/f1/2e/f3f12ea6932202d4193b5f7adcff0fe4.jpg';
    } else if (use === 'tmbnl') {
        return 'https://www.graphicsprings.com/filestorage/stencils/a566b9c474a87c9e5f516c7176bc47ae.svg';
    } else if (use === 'srnm') {
        return `user${Math.floor(Math.random() * 100000)}`;
    } else if (use === 'des') {
        return 'No Description provided for this recipe';
    } else if (use === 'ste') {
        return ['No Steps provided for this Recipe'];
    }
}
function validityChecker(link, use) {
    if (use === 'des' && link !== null) {
        return link.text;
    } else if (
        link === undefined ||
        link === null ||
        link === '' ||
        link === 'a630f43c-604c-4c6e-a8f6-64c76e3246cc'
    ) {
        return passer(use);
    } else if (use !== 'des') {
        return link;
    }
}

export default validityChecker;
