//********************** Loading animation *********************!/
function load() {
    const x = document.querySelector('#recipe-area');
    x.style.display = 'unset';
    x.innerHTML = `
        <div id="loader-container">
        <div>
            <div id="loader"></div>
            <p>Loading...</p>
        </div>
        </div>
        `;
}

export default load;
