;EACF;EM/jEU;IACI,kBAAA;IACA,gBAAA;IACA,iBAAA;IACA,kBAAA;IACA,SAAA;ENikEd;EM3jEE;IACI,oCAhRM;EN60EZ;EM5jEM;IACI,yBAAA;IACA,mCAlRE;ENg1EZ;EM7jEU;IACI,YAAA;IACA,gBAAA;IACA,iBAAA;IACA,oBAAA;EN+jEd;EM3jEU;IACI,YAAA;IAEA,aAAA;IACA,uBAAA;IACA,mBAAA;EN4jEd;EM3jEc;IACI,YAAA;IACA,WAAA;IACA,2BAAA;IACA,kBAAA;IACA,6BAAA;EN6jElB;EM5jEkB;IACI;MACI,0BAAA;IN8jExB;IM5jEoB;MACI,yBAAA;IN8jExB;EACF;EMzjEc;IACI,yBAAA;IACA,WAAA;IACA,aAAA;IACA,mBAAA;EN2jElB;EMzjEc;IACI,SAAA;IACA,UAAA;IACA,WAAA;IACA,oBAAA;EN2jElB;EMzjEkB;IACI,iBAAA;IACA,eAAA;EN2jEtB;EMvjEU;IAEI,aAAA;ENwjEd;EMvjEc;IACI,YAAA;IACA,WAAA;IACA,kBAAA;ENyjElB;EMvjEc;IACI,eAAA;IACA,cAAA;IACA,iBAAA;ENyjElB;EMtjEU;IACI,aAAA;IACA,oBAAA;IACA,gCAAA;ENwjEd;EMtjEU;IAEI,uBAAA;IACA,iBAAA;IACA,mBAAA;IACA,yCAAA;ENujEd;EMtjEc;IACI,gBAAA;IACA,oBAAA;IACA,cAAA;ENwjElB;EMtjEc;IACI,aAAA;IACA,cAAA;IACA,gBAAA;IACA,yCAAA;ENwjElB;EMnjEkB;IACI,aAAA;IACA,uBAAA;ENqjEtB;EMpjEsB;IACI,mBAAA;IACA,uBAAA;IACA,gBAAA;IACA,gBAAA;ENsjE1B;EMjjEU;IACI,aAAA;IACA,aAAA;IACA,gBAAA;ENmjEd;EMljEc;IACI,iBAAA;IAKA,uBAAA;IACA,YAAA;IACA,aAAA;IACA,yCAAA;IACA,mBAAA;ENgjElB;EMxjEkB;IACI,cAAA;IACA,mBAAA;EN0jEtB;EMhjEsB;IACI,+BAAA;ENkjE1B;EM9iEc;IACI,YAAA;ENgjElB;EM/iEkB;IACI,gBAAA;IACA,+BAAA;ENijEtB;EM5iEM;IACI,yBAAA;IACA,YAAA;EN8iEV;EM7iEU;IACI,yBAAA;EN+iEd;AACF","sourcesContent":["/////////// theme ///////////\r\n$primary: rgb(180, 253, 229);\r\n$success: rgb(60, 243, 182);\r\n\r\n* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\na {\r\n    text-decoration: none;\r\n    font-size: 1em;\r\n    color: unset;\r\n}\r\n\r\nbody {\r\n    font-family: Arial, Helvetica, sans-serif;\r\n    line-height: 1.5;\r\n    background-color: rgb(180, 253, 229);\r\n    color: black;\r\n    height: 100vh;\r\n}\r\n\r\n////////////// header /////////\r\nheader {\r\n    position: fixed;\r\n    top: 0;\r\n    width: 100%;\r\n    background-color: white;\r\n    padding: 0.2rem;\r\n    border-bottom: rgb(60, 243, 182) 3px solid;\r\n    z-index: 100;\r\n\r\n    .header-logo {\r\n        display: flex;\r\n        align-items: end;\r\n        h1 {\r\n            font-family: cursive;\r\n            font-weight: bold;\r\n        }\r\n        img {\r\n            padding: 0;\r\n            width: 70px;\r\n            height: 70px;\r\n        }\r\n    }\r\n}\r\n\r\n//////// grid-layout /////////////\r\n.grid-layout {\r\n    margin-top: 75px;\r\n    height: 100%;\r\n    display: grid;\r\n    grid-template-columns: repeat(4, 1fr);\r\n    grid-template-rows: repeat(4, 1fr);\r\n    // gap: 1rem;\r\n\r\n    .grid-layout-item {\r\n        // background-color: white;\r\n        // padding: 1rem;\r\n    }\r\n\r\n    // //////Aside///////////////\r\n    aside {\r\n        grid-row: 1/5;\r\n        // border-right: 3px solid darken($color: $success, $amount: 50%);\r\n        background-image: linear-gradient(to right, $success, darken($success, 20%));\r\n        position: fixed;\r\n        width: 395px;\r\n        height: 100%;\r\n        // z-index: -1;\r\n        .menu-header {\r\n            background-color: darken($color: $success, $amount: 50%);\r\n            height: 40px;\r\n            display: flex;\r\n            align-items: end;\r\n            h4 {\r\n                color: white;\r\n            }\r\n        }\r\n        .menu-items {\r\n            margin-top: 1.5rem;\r\n            padding: 20px;\r\n            height: 100vh;\r\n            p {\r\n                margin-bottom: 2rem;\r\n                border-bottom: 1px solid $primary;\r\n                a {\r\n                    font-weight: bold;\r\n                    color: rgb(2, 39, 2);\r\n                    // text-shadow: 3px 3px 3px gray;\r\n                    font-size: 1.7em;\r\n                    &:hover {\r\n                        color: lighten($color: rgb(2, 41, 2), $amount: 10%);\r\n                        transform: scale(1.1);\r\n                    }\r\n                }\r\n            }\r\n            .accordion {\r\n                margin-bottom: 2rem;\r\n                border-bottom: 1px solid $primary;\r\n                .accordion-item {\r\n                    border: none;\r\n                    outline: none;\r\n                    background-color: rgba(0, 0, 0, 0);\r\n                    #categories {\r\n                        background-color: rgba(0, 0, 0, 0);\r\n                        padding: 0;\r\n                        font-size: 0.9em;\r\n                        font-weight: bold;\r\n                        color: rgb(2, 39, 2);\r\n                        &:hover {\r\n                            color: lighten($color: rgb(2, 41, 2), $amount: 10%);\r\n                        }\r\n                        &:focus {\r\n                            outline: none;\r\n                            border: none;\r\n                        }\r\n                        // margin-bottom: ;\r\n                    }\r\n                }\r\n                #panelsStayOpen-collapseTwo {\r\n                    .accordion-body {\r\n                        background-color: rgba(0, 0, 0, 0);\r\n\r\n                        .accordion-body-item {\r\n                            display: flex;\r\n                            align-items: end;\r\n                            // border: 1px solid black;\r\n                            margin-bottom: 0.5rem;\r\n                            img {\r\n                                width: 40px;\r\n                                height: 40px;\r\n                                border-radius: 50%;\r\n                                border: 3px solid rgb(2, 39, 2);\r\n                            }\r\n                            p {\r\n                                padding: 0;\r\n                                margin: 0;\r\n                                font-size: 0.8em;\r\n                                align-self: end;\r\n                                border: none;\r\n                                margin-left: 1rem;\r\n                                // border: 1px solid black;\r\n                            }\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n\r\n            #log-out {\r\n                padding-top: 110px;\r\n                border-bottom: white 2px solid;\r\n                text-align: center;\r\n                a {\r\n                    color: rgba(0, 0, 0, 0.8);\r\n                    font-weight: normal;\r\n                    font-size: 1.3em;\r\n                    text-shadow: none;\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    // ////////main////////////////\r\n    main {\r\n        grid-column: 2/5;\r\n        grid-row: 1/4;\r\n        padding: 20px;\r\n\r\n        #search-bar {\r\n            margin-bottom: 2rem;\r\n        }\r\n\r\n        #recipe-area {\r\n            // display: grid;\r\n            grid-template-columns: repeat(3, 1fr);\r\n            gap: 1.5rem;\r\n        }\r\n\r\n        .recipe-item {\r\n            box-shadow: 7px 7px 20px black;\r\n            background-color: white;\r\n            // height: 300px;\r\n            border-radius: 8px;\r\n            border: $success 1px solid;\r\n            overflow: hidden;\r\n            img {\r\n            }\r\n            .thumbnail {\r\n                width: 100%;\r\n                max-height: 227px;\r\n                min-height: 227px;\r\n            }\r\n            .dish-name {\r\n                font-size: 1.5em;\r\n                font-weight: bold;\r\n                opacity: 70%;\r\n                margin: 1rem 0;\r\n                border-bottom: 1px solid $primary;\r\n                padding-bottom: 0.5rem;\r\n            }\r\n            .profile-pic-name {\r\n                display: flex;\r\n                justify-content: start;\r\n                align-items: center;\r\n                img {\r\n                    border: 2px solid darken($color: $success, $amount: 20%);\r\n                    width: 70px;\r\n                    height: 70px;\r\n                    border-radius: 50%;\r\n                }\r\n                .profile-name {\r\n                    align-self: end;\r\n                    margin-left: 0.6rem;\r\n                }\r\n            }\r\n        }\r\n        .rating-container {\r\n            display: flex;\r\n            justify-content: end;\r\n            padding: 10px 0;\r\n        }\r\n    }\r\n}\r\n\r\n////////LOADING Style/////////////\r\n#loader-container {\r\n    // border: 1px solid black;\r\n    width: 100%;\r\n    min-height: 600px;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    z-index: 100;\r\n    div {\r\n        text-align: center;\r\n        // border: 1px solid black;\r\n        position: relative;\r\n        height: 200px;\r\n        #loader {\r\n            border: darken($color: $success, $amount: 50%) dotted 10px;\r\n            height: 90px;\r\n            width: 90px;\r\n            border-radius: 50%;\r\n            position: absolute;\r\n            top: 0;\r\n            animation: rotate 2s infinite;\r\n            @keyframes rotate {\r\n                0% {\r\n                    transform: rotate(-180deg);\r\n                }\r\n                100% {\r\n                    transform: rotate(180deg);\r\n                }\r\n            }\r\n        }\r\n        p {\r\n            margin-top: 1.5rem;\r\n            font-size: 1.5em;\r\n            font-weight: bold;\r\n            position: absolute;\r\n            bottom: 0;\r\n        }\r\n    }\r\n}\r\n\r\n//////// Modal /////////////\r\n.modal-content {\r\n    background-color: $primary;\r\n    .modal-header {\r\n        text-transform: uppercase;\r\n        background-color: $success;\r\n        h5 {\r\n            color: white;\r\n            font-size: 1.5em;\r\n            font-weight: bold;\r\n            font-family: cursive;\r\n        }\r\n    }\r\n    .modal-body {\r\n        #modal-loading {\r\n            height: 100%;\r\n            // border: 1px solid black;\r\n            display: flex;\r\n            justify-content: center;\r\n            align-items: center;\r\n            #modal-loader-icon {\r\n                height: 90px;\r\n                width: 90px;\r\n                border: darken($color: $success, $amount: 50%) dotted 10px;\r\n                border-radius: 50%;\r\n                animation: rotate 2s infinite;\r\n                @keyframes rotate {\r\n                    0% {\r\n                        transform: rotate(-180deg);\r\n                    }\r\n                    100% {\r\n                        transform: rotate(180deg);\r\n                    }\r\n                }\r\n            }\r\n        }\r\n        #thumbnail-and-recipe-name {\r\n            img {\r\n                border: 5px solid darken($color: $success, $amount: 25%);\r\n                width: 100%;\r\n                height: 500px;\r\n                border-radius: 10px;\r\n            }\r\n            div {\r\n                margin: 0;\r\n                padding: 0;\r\n                width: 100%;\r\n                padding-bottom: 10px;\r\n                // border-bottom: 1px solid white;\r\n                #modal-dish-name {\r\n                    text-align: start;\r\n                    font-size: 32px;\r\n                }\r\n            }\r\n        }\r\n        #maker-profile-pic-rating {\r\n            // border: 1px solid black;\r\n            display: flex;\r\n            img {\r\n                height: 70px;\r\n                width: 70px;\r\n                border-radius: 50%;\r\n            }\r\n            p {\r\n                align-self: end;\r\n                font-size: 1em;\r\n                margin-left: 1rem;\r\n            }\r\n        }\r\n        .rating-container {\r\n            display: flex;\r\n            justify-content: end;\r\n            border-bottom: 2px solid lighten($success, 40%);\r\n        }\r\n        #description-container {\r\n            // padding: 50px;\r\n          