const minText = text => {
    const textF = [];
    const tS = text.split(' ');
    if (tS.length > 6) {
        while (textF.length <= 5) {
            textF.push(tS[0]);
            tS.splice(0, 1);
        }

        textF.push('...');
        return textF.join(' ');
    } else {
        return text;
    }
};

// export default minText;
module.exports = minText;
