//********************** Imports *********************!/
//////////// Bootstrap styles /////////////
import '../node_modules/bootstrap-icons/font/bootstrap-icons.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import '../node_modules/bootstrap/dist/js/bootstrap.bundle';

//////////// Custom Styles /////////////
import './style/mainStyle.scss';

//////////// Custom Tools /////////////
// import './js/warner';
import './js/categories';
import './js/logoImagesSrc';
import validityChecker from './js/validityChecker';
import loading from './js/loading';
import loadModal from './js/modalLoading';
import minText from './js/textMinimizer';

//********************** Api *********************!/
const api = {
    ////////////FEED LIST/////////////
    feedList: {
        link: 'https://yummly2.p.rapidapi.com/feeds/list?limit=30&start=0',
        options: {
            method: 'GET',
            headers: {
                'X-RapidAPI-Key': 'e7b4402e3fmshfdb4aa7589ea9fdp1c03cbjsna3ba8d1903cd',
                'X-RapidAPI-Host': 'yummly2.p.rapidapi.com',
            },
        },
    },

    ////////////CATEGORY LIST/////////////
    categoryList: {
        link: 'https://yummly2.p.rapidapi.com/categories/list',
        options: {
            method: 'GET',
            headers: {
                'X-RapidAPI-Key': 'e7b4402e3fmshfdb4aa7589ea9fdp1c03cbjsna3ba8d1903cd',
                'X-RapidAPI-Host': 'yummly2.p.rapidapi.com',
            },
        },
    },
};
//********************** run load animation as soon as the page open *********************!/
loading();

//********************** sideBar functions *********************!/
////////////Dashbooard/////////////
const dashBoard = document.querySelector('#dashboard');
dashBoard.addEventListener('click', e => location.reload());

//**********************Display Recipes for Modal  *********************!/
const modalCatcher = document.querySelector('.modal-body');
function forModal(data) {
    console.log(data);
    modalCatcher.innerHTML = `
    <!-- ///////////////////// Recipe thumbnail and caption \\\\\\\\\\\\\\ -->
    <div id="thumbnail-and-recipe-name">
        <img
            src="${data.feed[0].display.images[0]}"
            id="modal-thumbnail-img"
            alt="thumbnail-image"
            class="image-fluid"
        />
        <div class="">
            <p id="modal-dish-name">
                ${data.feed[0].display.displayName}
            </p>
        </div>
    </div>
    <!-- ///////////////////// Maker profile pic and name \\\\\\\\\\\\\\ -->
    <div class="" id="maker-profile-pic-rating">
        <img
            src="${validityChecker(data.feed[0].display.profiles[0].pictureUrl, 'vtr')}"
            id="modal-profile-pic"
            alt=""
        />
        <p id="modal-maker-name">${validityChecker(
            data.feed[0].display.profiles[0].profileName,
            'srnm'
        )}</p>
    </div>
    <!-- ///////////////////// Rating presentation \\\\\\\\\\\\\\ -->
    <section class="rating-container">
        <p class="rating">rating: ${data.feed[0].content.details.rating}/5</p>
        <p class="star text-warning">
            <i class="bi bi-star-fill"></i>
        </p>
    </section>
    <!-- ///////////////////// recipe description \\\\\\\\\\\\\\ -->
    <section id="description-container">
        <div class="container">
            <h3>Description</h3>
            <p id="description">
                ${validityChecker(data.feed[0].content.description, 'des')}
            </p>
        </div>
    </section>
    <!-- ///////////////////// Recipe Video Presentation \\\\\\\\\\\\\\ -->
    <section id="video-container">
        <div class="container">
            <div id="recipe-video-container">
                <video
                    src=""
                    class="img-fluid"
                    autoplay
                    alt="Guide video"
                    id="recipe-video"
                ></video>
            </div>
        </div>
    </section>
    <!-- ///////////////////// ingredients \\\\\\\\\\\\\\ -->
    <section id="ing-and-step">
        <section id="ingredient-container">
            <h3>Ingredients</h3>
            <div class="container" id="ingredient-catcher">
                <p class="ingredient-item">1 Tbsp. olive oil</p>
            </div>
        </section>
        <!-- ///////////////////// Steps and guide \\\\\\\\\\\\\\ -->
        <section id="steps-and-guide">
            <h3>Guide and steps</h3>
            <div class="container">
                <ol id="steps-catcher">
                </ol>
            </div>
        </section>
    </section>
    `;

    ////////////Ouput ingredients/////////////
    const ingredient = data.feed[0].content.ingredientLines.map(x => x.ingredient);
    const ingredientCatcher = document.querySelector('#ingredient-catcher');
    ingredient.forEach(ing => {
        const catcher = document.createElement('p');
        catcher.className = 'ingredient-item';
        catcher.textContent = ing;

        ingredientCatcher.prepend(catcher);
    });

    //////////// Output Guide/steps /////////////
    const steps = validityChecker(data.feed[0].content.preparationSteps, 'ste');
    const stepsCatcher = document.querySelector('#steps-catcher');
    steps.forEach(ste => {
        const li = document.createElement('li');
        li.className = 'steps-item';

        li.textContent = ste;
        stepsCatcher.prepend(li);
    });

    //////////// Output Guide/steps /////////////
    const videoContainer = document.querySelector('#video-container');
    const video = document.querySelector('#recipe-video');
    if (data.feed[0].content.hasOwnProperty('videos')) {
        video.src = data.feed[0].content.videos.originalVideoUrl;
    } else {
        videoContainer.remove();
    }
}
//**********************Display Recipes *********************!/
const recipeCatcher = document.querySelector('#recipe-area');
function displayRecipes(data) {
    recipeCatcher.innerHTML = '';
    recipeCatcher.style.display = 'grid';
    const validRecipe = data.feed.filter(
        x => x.type !== 'generic_cta' && x.display.profiles !== undefined
    );
    validRecipe.forEach(feed => {
        let id = feed.content.details.id;
        const div = document.createElement('div');
        div.className = 'recipe-item';
        div.innerHTML = `
            <img
            src="${validityChecker(feed.display.images[0], 'tmbnl')}"
            alt="${feed.display.displayName}"
            data-bs-toggle="modal"
            data-bs-target="#modal"
            class="thumbnail"
        />
        <p class="d-none">${id}</p>
        <section>
            <div class="container-fluid">
                <p class="dish-name">${minText(feed.display.displayName)}</p>
                <section class="profile-pic-name">
                    <img
                        src="${validityChecker(feed.display.profiles[0].pictureUrl, 'vtr')}"
                        alt=""
                        class="profile-pic"
                    />
                    <p class="profile-name">${validityChecker(
                        feed.display.profiles[0].profileName,
                        'srnm'
                    )}</p>
                </section>
                <section class="rating-container">
                    <p class="rating">rating: ${feed.content.details.rating}/5</p>
                    <p class="star text-warning">
                        <i class="bi bi-star-fill"></i>
                    </p>
                </section>
            </div>
        </section>
        `;

        recipeCatcher.append(div);
    });
}
//**********************catch recipes for recipe area*********************!/
async function fetchRecipes(link, param) {
    try {
        const recipes = await fetch(link, param);
        const res = await recipes.json();
        displayRecipes(res);
    } catch (err) {
        console.error(err);
    }
}

//**********************catch recipes for Recipe Modal*********************!/
async function fetchRecipesModal(link, param) {
    try {
        const recipes = await fetch(link, param);
        const res = await recipes.json();
        forModal(res);
    } catch (err) {
        console.error(err);
    }
}

//********************** output the recipes as soon as the page load *********************!/
fetchRecipes(api.feedList.link, api.feedList.options);

//********************** Search Recipes *********************!/
const searchBox = document.querySelector('#recipe-search');
const searchBtn = document.querySelector('#search-btn');

searchBtn.addEventListener('click', e => {
    ////////////Run Load Animation/////////////
    loading();
    ////////////FEED SEARCH/////////////
    const feedsSearch = {
        link: `https://yummly2.p.rapidapi.com/feeds/search?start=0&maxResult=18&maxTotalTimeInSeconds=7200&q=${searchBox.value
            .split(' ')
            .join('%')
            .toLowerCase()}`,
        options: {
            method: 'GET',
            headers: {
                'X-RapidAPI-Key': 'e7b4402e3fmshfdb4aa7589ea9fdp1c03cbjsna3ba8d1903cd',
                'X-RapidAPI-Host': 'yummly2.p.rapidapi.com',
            },
        },
    };
    fetchRecipes(feedsSearch.link, feedsSearch.options);
});

//********************** Categories *********************!/
const accordionBody = document.querySelector('.accordion-body');
accordionBody.addEventListener('click', e => {
    if (e.target.classList.contains('category-item')) {
        ////////////Run Load Animation/////////////
        loading();
        searchBox.value = e.target.textContent;
        ////////////FEED SEARCH/////////////
        const feedsSearch = {
            link: `https://yummly2.p.rapidapi.com/feeds/search?start=0&maxResult=18&maxTotalTimeInSeconds=7200&q=${searchBox.value
                .split(' ')
                .join('%')
                .toLowerCase()}`,
            options: {
                method: 'GET',
                headers: {
                    'X-RapidAPI-Key': 'e7b4402e3fmshfdb4aa7589ea9fdp1c03cbjsna3ba8d1903cd',
                    'X-RapidAPI-Host': 'yummly2.p.rapidapi.com',
                },
            },
        };

        fetchRecipes(feedsSearch.link, feedsSearch.options);
    }
});
//********************** Recipe on click *********************!/
recipeCatcher.addEventListener('click', e => {
    if (e.target.classList.contains('thumbnail')) {
        const targId = e.target.nextElementSibling.textContent;

        const similarities = {
            options: {
                method: 'GET',
                headers: {
                    'X-RapidAPI-Key': 'e7b4402e3fmshfdb4aa7589ea9fdp1c03cbjsna3ba8d1903cd',
                    'X-RapidAPI-Host': 'yummly2.p.rapidapi.com',
                },
            },
            link: `https://yummly2.p.rapidapi.com/feeds/list-similarities?limit=18&start=0&id=${targId}`,
        };

        fetchRecipesModal(similarities.link, similarities.options);
        loadModal();
    }
});
//********************** Close Modal *********************!/
const closeModal = document.querySelector('#close-modal').addEventListener('click', e => {
    modalCatcher.innerHTML = '';
});
