const categoryCatcher = document.querySelector('.accordion-body');

$('#see-more-btn').on('click', e => {
    categoryCatcher.innerHTML = '';
    $('#favorite-dishes').slideUp(100);
    $('#tutorial').fadeOut(100);
    $('#setting').fadeOut(100);
    $('#log-out').fadeOut(100);
    e.target.parentElement.parentElement.remove();
    categories.forEach(e => {
        const div = document.createElement('div');
        div.className = 'accordion-body-item';
        div.innerHTML = `
        <img
            src="${e.icon}"
            alt=""
        />
        <p>
            <a href="#" class="category-item"> ${e.item}</a>
        </p>
    `;

        categoryCatcher.prepend(div);
    });
});
